/* Import fonts from Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Gwendolyn&family=Great+Vibes&family=Cormorant+Infant&family=Parisienne&display=swap');

/* h1 { */
  /* font-family: 'Cormorant Infant', cursive; */
  /* font-size: 5rem; */
  /* font-weight: 300; */
/* } */
::-webkit-scrollbar {
  display: none;
}

h2 {
  font-family: 'Abhaya Libre', cursive;
  font-size: 2rem;
}

.invite-container {
  font-family: "Georgia", serif;
  color: #2b2b2b;
  text-align: center;
  /* padding: 2rem; */
  /* background-color: #fdfdfd; */
}

.navbar {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
}

.navbar ul {
  list-style: none;
  display: flex;
  gap: 2rem;
  padding: 0;
}

.navbar li {
  font-size: 1.3rem;
  font-weight: bold;
  font-family: 'Cormorant Infant', cursive;
  cursor: pointer;
}

.invite-content {
  margin-top: 2rem;
}

.invite-heading {
  font-size: 1rem;
  color: #7a7a7a;
}

.names {
  font-size: 5rem;
  font-weight: 350;
  margin: 1rem 0;
}

.guest {
  font-family: 'Gwendolyn', cursive;
  /* font-size: 3rem; */
  /* font-weight: normal; */
  margin: 1rem 0;
}

.guest-text {
  font-family: 'Gwendolyn', cursive;
  font-size: 1.5rem;
  color: #6B564D;
}

.and {
  font-family: 'Great Vibes', cursive;
  /* font-size: 4rem; */
  color: #a8a8a8;
  font-weight: 300;
}

.date {
  font-size: 1.5rem;
  font-weight: bold;
}

.title-invite {
  font-size: 1.6rem;
  font-weight: 100;
}

.date-number {
  font-size: 2rem;
  margin: 0 0.5rem;
}

.lorem-text {
  font-size: 0.9rem;
  color: #7a7a7a;
  margin: 1rem 0;
}

.info-button {
  font-size: 1rem;
  color: #2b2b2b;
  background: transparent;
  border: 1px solid #2b2b2b;
  padding: 0.5rem 1.5rem;
  cursor: pointer;
  border-radius: 5px;
  transition: background 0.3s, color 0.3s;
}

.info-button:hover {
  background: #2b2b2b;
  color: #fff;
}

.footer {
  margin-top: 2rem;
}

.decorations {
  margin-top: 1rem;
  opacity: 0.6;
}

.leaf {
  display: inline-block;
  background: url("leaf-bg.jpg"); /* Replace with your floral decoration */
  width: 50px;
  height: 50px;
}

.background-image-container-2 {
  height: 100vh; /* Full height of the viewport */
  justify-content: center;
  align-items: center;
  text-align: center;
}

@media (max-width: 1024px) {
  .background-image-container-2 {
    background-image: url('5888038-mb.jpg');
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    -ms-ie-background-size: cover;
  }
}

.responsive-br {
  display: none;
}

@media (max-width: 800px) {
  .responsive-br {
    display: block;
  }
}