html, body {
  height: 100%;
  margin: 0;
}


#background_wrap {
  z-index: -1;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-size: cover;
  background-image: url('components/leaf-bg.jpg');
  background-repeat: no-repeat;
  background-attachment: scroll;
}


@media (max-width: 1024px) {
  #background_wrap {
    background-image: url('components/5888038-mb.jpg');
  }
}